<template>
    <div>
        <div class="page-header">
            <div>
                <h1>Lottery Results</h1>
                <b-breadcrumb :items="items" class="bg-transparent justify-content-center"></b-breadcrumb>
            </div>
        </div>
        <latestWinner class="bg-white"></latestWinner>
        <lotteryResult class="bg-light"></lotteryResult>
        <subscribe></subscribe>
    </div>
</template>

<script>
    import lotteryResult from '@/components/include/lotteryResult'
    import latestWinner from '@/components/include/latestWinner'
    import subscribe from '@/components/include/subscribe'
    export default {
        name:'Results',
        components:{ lotteryResult, latestWinner ,subscribe},
        data(){
            return{
                items:[
                    {
                        text:'Home',
                        to:'/'
                    },
                    {
                        text:'Results',
                        to:'Results'
                    }   
                ]
            }
        }
    }
</script>

<style lang="css">
    .page-header{
        background-color:rgba(0, 0, 0, 0.1);
        height:300px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: #000;
    }
</style>


