<template>
    <div>
        <div class="winners">
            <b-container>
                <b-row>
                    <b-col md="8" class="mx-auto">
                        <div class="head text-center">
                            <h4 class="title">Latest Winner</h4>
                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Doloribus, alias? Ab, eveniet necessitatibus ut quidem,</p>
                        </div>
                    </b-col>
                </b-row>

                <b-row>
                    <b-col md="6" v-for="(data,key) in winners" :key="key">
                        <div class="winner-box">
                            <b-row>
                                <b-col md="4">
                                    <img :src="data.img">
                                </b-col>
                                <b-col md="8" class="align-self-center">
                                    <h4>{{ data.title}}</h4>
                                    <ul class="jackpot-number">
                                        <li>12</li>
                                        <li>23</li>
                                        <li>24</li>
                                        <li>26</li>
                                        <li>31</li>
                                    </ul>
                                    <h4>Wins: {{ data.amount }}</h4>
                                </b-col>
                            </b-row>
                            <span class="date">{{ data.date }}</span>
                        </div>
                    </b-col>
                </b-row>
            </b-container>
        </div>
    </div>
</template>

<script>
    export default {
        name:'latestWinner',
        data(){
            return{
                winners:[
                    {
                        date:'10 May',
                        img:require('@/static/choose1.png'),
                        title:'Latest Daily Draw',
                        amount:'$20000'
                    },
                    {
                        date:'12 May',
                        img:require('@/static/win2.png'),
                        title:'Latest Weekly Draw',
                        amount:'$20000'
                    },
                    {
                        date:'14 May',
                        img:require('@/static/choose2.png'),
                        title:'Latest Monthly Draw',
                        amount:'$20000'
                    },
                    {
                        date:'18 May',
                        img:require('@/static/win4.png'),
                        title:'Latest Jackpot Draw',
                        amount:'$20000'
                    },
                ]
            }
        }
    }
</script>

<style lang="css">
    .winners{
        padding:5rem 0;
    }
    .winner-box{
        background-color: white;
        margin-bottom: 2rem;
        border:1px solid #ccc;
        padding:40px;
        overflow: hidden;
        position: relative;
    }

    .winner-box img{
        width:140px;
    }

    .winner-box span{
        position: absolute;
        right: -50px;
        top: 22px;
        width: 180px;
        text-align: center;
        height: 34px;
        line-height: 34px;
        transform: rotate(45deg);
        z-index: 2;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 700;
        background-color: #c2185b;
        color: #Fff;
    }

    .winner-box .jackpot-number{
        display:flex;
        list-style: none;
        justify-content: space-between;
        width: 250px;
        padding-left: 0;
    }

    .winner-box .jackpot-number li{
        padding:.5rem;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background: darkviolet;
        color: #fff;
        width: 40px;
        height: 40px;
        font-weight: bold;
        font-size: 20px;
    }
</style>


