<template>
    <div>
        <div class="page-header">
            <div>
                <h1>About Us</h1>
                <b-breadcrumb :items="items" class="bg-transparent justify-content-center"></b-breadcrumb>
            </div>
        </div>
        <feature></feature>
        <howToStart></howToStart>
        <chooseUs></chooseUs>
        <clientReview></clientReview>
        <subscribe></subscribe>
    </div>
</template>

<script>
    import feature from '@/components/include/feature'
    import howToStart from '@/components/include/howToStart'
    import chooseUs from '@/components/include/chooseUs'
    import clientReview from '@/components/include/clientReview'
    import subscribe from '@/components/include/subscribe'

    export default {
        name:'About',
        components:{
            feature,
            howToStart,
            chooseUs,
            clientReview,
            subscribe
        },
        data(){
            return{
                items:[
                    {
                        text:'Home',
                        to:'/'
                    },
                    {
                        text:'About',
                        to:'/About'
                    }
                ],
            }
        }
    }
</script>

<style lang="css">
    .page-header{
        background-color:rgba(0, 0, 0, 0.1);
        height:300px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: #000;
    }
</style>


