<template>
    <div>
        <div class="client-review bg-light">
            <b-container>
                <b-row>
                    <b-col md="8" class="mx-auto">
                        <div class="head text-center">
                            <h4 class="title">Client Reviews</h4>
                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Doloribus, alias? Ab, eveniet necessitatibus ut quidem,</p>
                        </div>
                    </b-col>
                </b-row>
                <carousel :items="3" :dots="true" :nav="false" :loop="true" :margin="35" :center="true">
                    <div class="review-box" v-for="(data,key) in lists" :key="key">
                        <h5>{{ data.name }}</h5>    
                        <p class="text-muted mb-0">{{ data.post }}</p>
                        <div class="rating py-3">
                            <starOutline class="text-warning" :size="48" />
                            <starOutline class="text-warning" />
                            <starOutline class="text-warning" />
                            <starOutline class="text-warning" />
                            <starOutline class="text-warning" />
                        </div>
                        <p>
                            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Dignissimos eos quasi sit quisquam adipisci! Velit cupiditate quod quis vero.
                        </p>
                    </div>
                </carousel>
            </b-container>
        </div>
    </div>
</template>

<script>
    import carousel from 'vue-owl-carousel'
    import starOutline from 'vue-material-design-icons/starOutline.vue';
    export default {
        name: 'clientReview',
        components:{
            carousel, starOutline
        },
        data(){
            return{
                lists:[
                    {
                        name:'Charlotte',
                        post:'General Secretary'
                    },
                    {
                        name:'Charlotte',
                        post:'General Secretary'
                    },
                    {
                        name:'Charlotte',
                        post:'General Secretary'
                    },
                    {
                        name:'Charlotte',
                        post:'General Secretary'
                    }
                ]
            }
        }
    }
</script>

<style lang="css">
    .client-review{
        padding:5rem 0;
    }
    .head{
        margin-bottom: 3rem;
    }

    .head h4{
        font-size: 35px;
        font-weight: 600;
    }

    .text-purple{
        color:blueviolet
    }

    .review-box{
        max-width:360px;
        padding:30px;
        background: #fff;
        box-shadow:0 0 10px -5px #aaa;
    }

    .review-box .rating span{
        font-size:1.5rem;
    }

    .review-box h5{
        color:darkslateblue
    }

    .review-box h5 + p{
        font-size:.95rem;
    }

    .review-box p{
        font-size: .86rem;
        line-height: 2;
    }

    .owl-theme .owl-dots .owl-dot:focus{
        outline: none;
    }

    .owl-theme .owl-dots .owl-dot.active span{
        width:30px;
        background-color:blueviolet;
    }

    .owl-carousel .owl-stage-outer{
        padding-top: 1%;
        padding-bottom: 5%;
    }
    .owl-theme .owl-dots .owl-dot span{
        background-color: #777;
        width:8px;
        transition: .5s ease;
        height:8px;
        margin-left:3px;
        margin-right:3px;
    }
</style>


