<template>
    <div>
        <div class="banner">
            <img src="@/static/banner-bg.jpg" class="bg">
            <b-container>
                <b-row>
                    <b-col md="5" class="content">
                        <span class="text-uppercase">Play & Win Lottery easily</span>
                        <h4 class="text-uppercase">Play Lottery Everyday and Win <b>prizes</b></h4>
                        <p>We develop effective plans to move your customers behaviour. customer can be alwayes his profit.</p>
                        <b-button  size="lg">Play Now</b-button>
                    </b-col>
                    
                </b-row>
            </b-container>  
        </div>
        <howToStart></howToStart>
        <feature></feature>
        <lottery class="bg-light"></lottery>  
        <chooseUs></chooseUs>
        <latestWinner class="bg-light"></latestWinner>
        <lotteryResult></lotteryResult>
        <clientReview></clientReview>
        <subscribe></subscribe>
    </div>  
</template>

<script>

import howToStart from '@/components/include/howToStart'
import feature from '@/components/include/feature'
import lottery from '@/components/include/lottery'
import chooseUs from '@/components/include/chooseUs'
import lotteryResult from '@/components/include/lotteryResult'
import latestWinner from '@/components/include/latestWinner'
import clientReview from '@/components/include/clientReview'
import subscribe from '@/components/include/subscribe'

export default {
    name:'Home',
    components: { howToStart, feature, lottery, chooseUs, lotteryResult, latestWinner, clientReview, subscribe},
}
</script>

<style lang="css" scoped>
    .banner{
        padding:5rem 0;
        width:100%;
        height:650px;
        position: relative;
    }

    .banner .bg{
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        width:100%;
        height: 100%;
        object-fit: cover;
    }
    .content span{
        margin:1rem 0;
        font-size: 1.2rem;
        font-weight: bold;
        color: #c2185b;
        display:inline-block
    }
    .content h4{
        font-size: 3rem;
        font-weight: 300;
        color:#009a;
        margin:0 0 1.25rem;
    }

    .content button{
        margin:1em 0;
        background-color:#c2185b;
        border:none;
        padding:.75rem 1.5rem;
    }

    .title{
        font-size: 2.5rem;
        color: #222;
    }

    .head{
        margin-bottom: 3rem;
    }

    .head h4{
        font-size: 35px;
        font-weight: 600;
    }
</style>



