                        <label>{{ data.value.id }}</label>
                    </template>
                    <template v-slot:cell(date)="data">
                        {{ data.value }}
                    </template>
                    <template v-slot:cell(number)="data">
                        <ul class="number-list">
                            <li>{{data.value.num1}}</li>
                            <li>{{data.value.num2}}</li>
                            <li>{{data.value.num3}}</li>
                            <li>{{data.value.num4}}</li>
