<style lang="css" >
    .results{
        padding:5rem 0;
    }
    .result-table td,
    .result-table th{
        padding:8px 10px 8px 30px;
        height:80px;
        vertical-align: middle !important; 
    }
    .result-table .number-list{
        display: flex;
        width: 180px;
        justify-content: space-between;
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }

    .result-table .number-list li{
        width: 30px;
        height:30px;
        background: #988296;
        color: #fff;
        font-size:13px;
        font-weight:600;
        border-radius: 50%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }
    .result-table label{
        margin-bottom: 0;
    }
    .result-table img{
        width: 40px;
        border-radius: 50%;
        margin-right: 10px;
    }

    .result-table th{
        text-transform: uppercase;
    }
</style>




