<template>
    <div>
        <div class="page-header">
            <div>
                <h1>Lottery</h1>
                <b-breadcrumb :items="items" class="bg-transparent justify-content-center"></b-breadcrumb>
            </div>
        </div>
        <lottery></lottery>
        <lotteryResult class="bg-light"></lotteryResult>   
        <subscribe></subscribe>
    </div>
</template>

<script>
    import lottery from '@/components/include/lottery'
    import lotteryResult from '@/components/include/lotteryResult'
    import subscribe from '@/components/include/subscribe'
    export default {
        name:'Lottery',
        components:{
            lottery, lotteryResult, subscribe
        },
        data(){
            return{
                items:[
                    {
                        text:'Home',
                        to:'/'
                    },
                    {
                        text:'Lottery',
                        to:'/Lottery'
                    }
                ]
            }
        }
    }
</script>

<style lang="css" >
    .page-header{
        background-color:rgba(0, 0, 0, 0.1);
        height:300px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: #000;
    }
</style>


