<template>
    <div>
        <div class="subscribe">
            <b-container>
                <b-row>
                    <b-col md="8" class="mx-auto">
                        <div class="head text-center">
                            <h4 class="title">Subscribe Newsletter</h4>
                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Doloribus, alias? Ab, eveniet necessitatibus ut quidem,</p>
                        </div>
                        <b-form>
                            <b-input-group class="myInput">
                                <b-form-input type="email" placeholder="Email Address" size="md"></b-form-input>
                                <b-input-group-append>
                                    <b-button class="bg-purple px-5">SUBSCRIBE</b-button>
                                </b-input-group-append>
                            </b-input-group>
                        </b-form>
                    </b-col>
                </b-row>
                <div class="my-5 pt-4">
                    <carousel :items="6" :dots="false" :nav="false" :loop="true">
                            <div v-for="(brand,key) in icons" :key="key">
                                <img :src="brand.icon"  class="img">
                            </div>
                    </carousel>
                </div>
            </b-container>
        </div>
    </div>
</template>

<script>
    import carousel from 'vue-owl-carousel'
    export default {
        name:'subsribe',
        components:{
            carousel
        },
        data(){
            return{
                icons:[
                    {
                        icon:require('@/static/payment1.png')
                    },
                    {
                        icon:require('@/static/payment2.png')
                    },
                    {
                        icon:require('@/static/payment3.png')
                    },
                    {
                        icon:require('@/static/payment4.png')
                    },
                    {
                        icon:require('@/static/payment5.png')
                    },
                    {
                        icon:require('@/static/payment6.png')
                    },
                    {
                        icon:require('@/static/payment7.png')
                    },
                    {
                        icon:require('@/static/payment8.png')
                    },
                ]
            }
        }
    }
</script>

<style lang="css">
    .subscribe{
        padding:5rem 0
    }

    .myInput .form-control{
        padding:10px 20px;
        height:60px;
        font-size: .8rem;
    }

    .myInput .form-control:focus{
        box-shadow: none;

    }

    .myInput{
        box-shadow:0 5px 25px rgba(0, 0, 0, 0.1);
    }

    .bg-purple{
        background-color:#7b1fa2;
    }
    .img{
        width:100px !important;
    }
</style>


