<template>
    <div>
        <div class="lottery-card">
            <b-container>
                <b-row>
                    <b-col md="8" class="mx-auto">
                        <div class="head text-center">
                            <h4 class="title">Jackpot Lottery</h4>
                            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Doloribus, alias? Ab, eveniet necessitatibus ut quidem,</p>                            
                        </div>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col md="12">
                        <carousel  :loop="true" :margin="25" :dots="false">
                            <b-card class="lottery" v-for="(item,key) in details" :key="key">
                                <span class="wrap">20CD</span>
                                <h5>Win Prize</h5>
                                <h5><b>{{item.price}}</b></h5>
                                <b-img :src='item.img' class="img-fluid"></b-img>

                                <h4 class="my-2">{{item.title}}</h4>
                                <label class="my-2">Next Draw: {{item.nextDraw}}</label>

                                <b-button class="butn my-2">Play Now</b-button>
                            </b-card>
                        </carousel>
                    </b-col>
                </b-row>
            </b-container>
        </div>
    </div>
</template>

<script>
import carousel from 'vue-owl-carousel'
    export default {
        name:'lottery',
        components:{
            carousel
        },
        data(){
            return{
                details:[
                    {
                       price:'$21000k',
                       title:'Winter Jackpot',
                       img: require('@/static/prize1.png'),
                       nextDraw:'24 May, 2020'
                    },
                    {
                       price:'$5000k',
                       title:'Las Vegas Jackpot',
                       img: require('@/static/prize2.png'),
                       nextDraw:'10 May, 2020'
                    },
                    {
                       price:'$9000k',
                       title:'Powerball Jackpot',
                       img: require('@/static/prize3.png'),
                       nextDraw:'15 May, 2020'
                    },
                    {
                       price:'$7500k',
                       title:'London Jackpot',
                       img: require('@/static/prize2.png'),
                       nextDraw:'24 May, 2020'
                    }
                ]
            }
        }
    }
</script>

<style lang="css">
    .lottery-card{
        padding:5rem 0;
    }
    
    .lottery{
        max-width:360px;
        padding:30px 20px;
        text-align: center;
    }
    .lottery img{
        display:inline-block;
        margin:1em auto;
        max-width:140px;
        height:140px;
    }

    .butn{
        background-color: #1FC157;
        padding:8px 15px;
        border:1px solid #1FC157;
        font-size: 15px;
    }

    .butn:hover{
        background-color: transparent;
        color: #1FC157;
    }

    .lottery span{
        background-color: #7b1fa2;
        position: absolute;
        left: 0px;
        top: 10px;
        color: #fff;
        font-weight: 600;
        font-size: 18px;
        padding: 2px 15px;
        line-height: 26px;
        border-radius: 0px 3px 3px 0px;
    }

</style>


