<template>
    <div>
        <div class="top-nav py-3">
            <b-container>
                <b-row class="align-items-center">
                    <b-col md="6">
                        <ul class="list-unstyled d-flex w-50 justify-content-between mb-0">
                            <li>
                                <b-icon-clock></b-icon-clock>
                                <label>Live Support</label>
                            </li>
                            <li>
                                <b-icon-envelope></b-icon-envelope>
                                <label>info@lotery.com</label>
                            </li>
                        </ul>
                    </b-col>
                    <b-col md="6" class="text-right">
                        <b-dropdown text="English" right size="sm" class="mr-2 " toggle-class="text-white" variant="transparent">
                            <b-dropdown-item>Deutch</b-dropdown-item>
                            <b-dropdown-item>Espanol</b-dropdown-item>
                            <b-dropdown-item>Francias</b-dropdown-item>
                            <b-dropdown-item>Urdu</b-dropdown-item>
                        </b-dropdown>
                        <a to="">
                            <b-icon-person></b-icon-person>
                            Login
                        </a>
                    </b-col>
                </b-row>
            </b-container>
        </div>
        <b-navbar toggleable="lg" type="light" variant="light" class="py-3" :class="{fixed:isFixed}">
            <b-container>
                <b-navbar-brand to="/">
                    <span class="text-dark font-weight-bold">LOGO</span><span class="text-success">GAME</span>
                </b-navbar-brand>
                <b-navbar-toggle target="navbar-toggle-collapse">
                    <template v-slot:default="{ expanded }">
                        <b-icon v-if="expanded" icon="chevron-bar-up"></b-icon>
                        <b-icon v-else icon="chevron-bar-down"></b-icon>
                    </template>
                </b-navbar-toggle>

                <b-collapse id="navbar-toggle-collapse" is-nav>
                    <b-navbar-nav class="ml-auto mr-5 myNavbar">
                        <b-nav-item to="/">Home</b-nav-item>
                        <b-nav-item to="About">About</b-nav-item>
                        <b-nav-item to="Lottery">Lottery</b-nav-item>
                        <b-nav-item to="Results">Results</b-nav-item>
                        <b-nav-item to="Contact">Contact</b-nav-item>
                    </b-navbar-nav>

                    <b-navbar-nav>
                        <b-nav-item>
                            <b-button class="py-2 join-btn rounded-0 px-4 text-uppercase font-weight-bold" size="sm">Join Now</b-button>
                        </b-nav-item>
                    </b-navbar-nav>
                </b-collapse>
            </b-container>
        </b-navbar>
    </div>
</template>

<script>
    import  $ from 'jquery'

    export default {
        name: 'Header',
        mounted() {
            console.log(this.$options.name+' component successfully mounted');
            let $window = $(window),
            sticky = $('.navbar'),
            stickyTop = sticky.offset().top;
            $window.on('scroll', function(){
                if ($window.scrollTop() >= stickyTop) {
                    this.isFixed = true
                    
                }
                else {
                    this.isFixed = false
                }
            })
        },
        data(){
            return{
                isFixed:false
            }
        }
    }
</script>

<style lang="css" scoped>
    .top-nav{
        background:linear-gradient(to left, #c2185b,#7b1fa2);
        color:#fff
    }

    label{
        margin-bottom:0
    }

    .join-btn{
        background-color:#7b1fa2;
    }

    .myNavbar li a{
        font-weight: 600;
        margin-left:1rem;
        margin-right:1rem;
        font-size: .9rem;
    }
</style>



